
.vue-oss-upload-input {
  position: relative;
  display: inline-block;
  text-align: center;
  width: 70px;
  height: 70px;
  border-radius: 5px;
  border: 2px dashed #aaaaaa;
  line-height: 66px;
}
.vue-oss-upload-input i {
  font-style: normal;
  font-size: 36px;
  color: #aaaaaa;
  display: block;
}
.vue-oss-upload-input input {
  width: 100%;
  height: 100%;
  /* margin-top: -66px; */
  opacity: 0;
  position: absolute;
  padding: 0;
  top: 0;
  left: 0;
}
